<template>
  <v-app>
    <core-filter/>

    <core-toolbar/>

    <core-drawer/>

    <core-view/>
  </v-app>
</template>
<script>
import gql from 'graphql-tag'
export default {
  apollo: {
    // Subscriptions
    $subscribe: {
      // When a tag is added
      newEvent: {
        query: gql`
          subscription {
            newEvent {
              _id
              active
              cordinator
              guests {
                _id
                name
              }
              memberAsist {
                name
                _id
              }
              createdBy {
                _id
              }
              description
              endDate
              eventDate
              orgScope
              place
              staffAuv
              staffDarte
              staffEci
              staffMav
              staffProto
              staffVar
              title
            }
          }
        `,
        // Reactive variables
        variables() {
          // This works just like regular queries
          // and will re-subscribe with the right variables
          // each time the values change
        },
        // Result hook
        result(data) {
          console.log(data)
          this.$store.commit('addEvent', data.data.newEvent)
        }
      }
    }
  },
  methods: {},
  mounted() {
    if (this.$store.state.auth.token) {
      if (!this.$store.getters.getUserSub) {
        this.$store.dispatch('subscribeWebPush')
      }
    }
  }
}
</script>

